@import "../helpers/_mixins.scss";
@import "../helpers/_variables.scss";

.contact-maps {
  .contact-maps__find-store {
    font-size: 14px;
    line-height: 35px;
    width: 100%;
    padding: 2px 30px;
    color: #fff;
    background: #dd4a4a;
    border-radius: 0;
    border: none;
    order: 3;
  }
}

.page-title.page-title--maps {
  padding: 39px 0 25px;
}

.contact-maps__inner {
  display: flex;
  flex-direction: column;
  padding-bottom: 16px;

  @include mq(ml) {
    flex-direction: row;
  }
}

.contact-maps__form {
  margin-bottom: 16px;
}

.contact-maps__form {
  &:last-child {
    margin: 0;
  }
}

.product-detail__sidebar.contact-maps__sidebar {
  width: 100%;
  margin-left: 0;

  @include mq(ml) {
    flex: 1 0 auto;
    width: 293px;
    padding-left: 15px;
  }

  .section-box {
    border: 1px solid #eeeeee;
  }
}

.product-card--results {
  .product-card__inner {
    flex-wrap: nowrap;
  }

  .product-card__view {
    flex: 1 0 auto;

    @include mq(ml) {
      width: 320px;
    }
  }

  .product-card__content {
    width: 100%;
    padding: 15px 0 0;

    @include mq(sm) {
      padding: 0 0 0 32px;
    }

    @include mq(ml) {
      padding: 0 55px 0 28px;
    }
  }

  .product-card__title {
    @include mq(ml) {
      display: flex;
      justify-content: space-between;
    }
  }
}

.section-box--results {
  padding: 18px 0 28px;

  @include mq(sm) {
    padding: 18px 55px 0 28px;
  }

  .section-box__title {
    padding: 0 0 14px;
  }

  .section-box__content {
    padding: 29px 0;

    @include mq(sm) {
      padding: 29px 0;
    }
  }
}

.section-box--maps {
  display: flex;
  width: 100%;

  iframe {
    width: 100%;
  }
}

.contact-maps__main {
  width: 100%;
}

.button--result-contact {
  min-width: 145px;
  border-radius: 22px;
  height: 45px;
}

.paginations--contact-us {
  &.product-detail {
    padding-bottom: 22px;
    background: none;

    @include mq(sm) {
      background: #f6f6f6;
    }
}

.paginations {
display: flex;
justify-content: center;
padding-bottom: 50px;
}

.button--pagination {
  display: flex;
  align-items: center;
  margin: 0 6px;
  color: #000;
  border-radius: 8px;
  background: transparent;
  padding: 3px 14px;
  border-color: $c-gray-1;

  &[disabled] {
    border-color: #cccccc;
    cursor: default;

    & i {
      border-color: #cccccc;
    }
  }

  &.active {
    color: #fff;
    background: #a7a7f5;
    border-color: #a7a7f5;
    cursor: default;
  }
}

.button--pagination-prev {
border: solid $c-black;
border-width: 0 1px 1px 0;
display: inline-block;
padding: 3px;
transform: rotate(135deg);
}

.button--pagination-next {
border: solid $c-black;
border-width: 0 1px 1px 0;
display: inline-block;
padding: 3px;
transform: rotate(-45deg);
}
}
