@import "../helpers/_mixins.scss";
@import "../helpers/_variables.scss";

.section-timeline {
  margin-top: 36px;
  padding: 0 0 8px;
}

.about-timeline {
  position: relative;
  margin-bottom: 30px;

  &:before {
    content: '';
    position: absolute;
    left: 5px;
    width: 2px;
    height: calc(100% + 8px);
    border-radius: 3px;
    background: #afafaf;
    z-index: 9;
  }

  @include mq(sm) {
    margin-bottom: 55px;

    &:before {
      content: '';
      position: absolute;
      left: 84px;
      width: 7px;
      height: calc(100% + 8px);
      border-radius: 3px;
      background: #afafaf;
      z-index: 9;
    }
  }
}

.about-timeline__wrapper {
  padding-left: 30px;

  @include mq(sm) {
    padding-left: 126px;
  }
}

.about-timeline__images {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: space-between;

  @include mq(sm) {
    flex-wrap: nowrap;
  }
}

.about-timeline__image {
  @include aspect(144, 357);
  width: 100%;
  margin-top: 10px;

  &:first-child {
    margin-top: 0;
  }

  @include mq(sm) {
    width: calc(100% / 3);
    margin-top: 0;
  }
}

.about-timeline__image--small {
  @include mq(sm) {
    @include aspect(156, 320);
    max-width: 320px;
  }
}

.about-timeline__image--large {
  background: #e7e7e7;

  @include mq(sm) {
    @include aspect(163, 560);
    flex: 1 0 auto;
    max-height: 156px;
    overflow: hidden;
  }

  & .image-lazy__img {
    transform: translateY(3px);
  }
}

.about-timeline__title {
  font-size: 24px;
  line-height: 35px;
  font-weight: 700;
  color: $c-red;

  &:before {
    content: "01";
    position: absolute;
    left: -2px;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    padding-top: 8px;
    color: $c-black-2;
    z-index: 11;

    @include mq(sm) {
      font-size: 40px;
      line-height: 35px;
      padding-top: 3px;
    }
  }

  &:after {
    content: '';
    position: absolute;
    left: 6px;
    width: 32px;
    height: 32px;
    border: 2px solid $c-red;
    border-radius: 50%;
    background: #fff;
    transform: translate(-50%);
    z-index: 10;
  }

  @include mq(sm) {
    &:after {
      content: '';
      position: absolute;
      left: 87px;
      width: 36px;
      height: 36px;
      background: $c-red;
      border: 8px solid $c-gray-2;
      border-radius: 50%;
    }
  }
}

.about-timeline__item {
  margin-top: 27px;

  &:nth-child(2) {
    .about-timeline__title {
      &:before {
        content: "02";
      }
    }
  }

  &:nth-child(3) {
    .about-timeline__title {
      &:before {
        content: "03";
      }
    }
  }

  &:nth-child(4) {
    .about-timeline__title {
      &:before {
        content: "04";
      }
    }
  }
}


.about-timeline__description {
  line-height: 21px;
  margin: 0;
  padding: 11px 0 17px;
  color: $c-black;
}

.section-header--type2 {
  margin: 52px 0 61px;

  &.section-header {
    &:before {
      left: calc(50% - 40px);
      bottom: -7px;
      width: 91px;
      height: 3px;
    }
  }

  &.section-header--book {
    margin: 0;
  }
}
.section--about-howto {
  padding: 47px 0 57px;
}
.about-howto {
  padding: 55px 0 0;

  & .home-howto__item-title {
    font-size: 18px;
    line-height: 24px;
    margin: 23px auto 0;
    color: #333333;
  }

  & .home-howto__item-text {
    margin-top: 13px;
  }

  & .home-howto__item-icon {
    width: 70px;
    height: 70px;
    border: solid 1px #bb3a28;
  }

  & .home-howto__item-inner {
    &:before {
      background-color: #bb3a28;
    }
  }

  .home-howto__item:last-child {
    margin: 0;
  }
}

.section--faq {
  max-width: 871px;
  margin: 0 auto;
  padding-bottom: 65px;
}
