@import "../helpers/_mixins.scss";
@import "../helpers/_variables.scss";

.page-title {
  padding: 30px 0 25px;

  @include mq(ml) {
    padding: 40px 0 35px;
  }
}

.page-title__text {
  position: relative;
  font-size: 22px;
  font-family: $font-roboto-bold;
  text-transform: uppercase;
  padding-bottom: 10px;

  @include mq(ml) {
    font-size: 30px;
    padding-bottom: 12px;
  }

  &:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 66px;
    height: 2px;
    background-color: $color-primary;
  }

  .page-header__title & {
    text-transform: none;
  }
}

.product-info {
  background-color: $color-white;
  padding-bottom: 20px;
}

.product__info-inner {
  display: flex;
  flex-direction: column;

  @include mq(sm) {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
  }
}

.product__info-gallery {
  border: 1px solid $border-color-primary;

  @include mq(sm) {
    width: 50%;
  }

  @include mq(ml) {
    width: 57%;
  }
}

.product__info-image {
  @include aspect(370, 666);
}

.product__info-detail {
  padding-top: 30px;

  @include mq(sm) {
    padding-top: 0;
    padding-left: 30px;
    width: 50%;
  }

  @include mq(ml) {
    padding-top: 15px;
    width: 43%;
  }
}

.product__info-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 1px solid $border-color-primary;
  padding-bottom: 15px;
  margin-bottom: 25px;
}

.product__info-title {
  flex: 1 1 0;

  h2 {
    font-family: $font-roboto-bold;
    font-size: 21px;
  }
}

.product__info-price {
  padding-top: 10px;

  b {
    font-size: 24px;
    color: #f50007;
    font-family: $font-roboto-medium;

    small {
      font-size: 14px;
      position: relative;
      top: -10px;
      margin-left: 4px;
    }
  }
}

.product__info-spec {
  & li {
    margin-bottom: 10px;
    display: flex;
  }

  strong {
    font-family: $font-roboto-medium;
    width: 25%;
  }

  span {
    width: 75%;
    padding-left: 10px;
  }
}

.product__info-utils {
  display: flex;
  margin-top: 30px;
}

.product__info-utils-title {
  background-color: $c-gray-5;
  padding: 25px 20px;
  color: $color-white;
  font-size: 16px;
  font-family: $font-roboto-bold;
}

.product__info-utils-list {
  display: flex;
  flex: 1 1 0;
  background-color: #eeeeee;
}

.product__info-utils-item {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 10px 5px;
  flex-direction: column;
  height: 100%;
}

.product__info-utils-icon {
  width: 25px;
}

.product__info-utils-text {
  font-size: 11px;
  margin-top: 5px;
}

.product__info-notice {
  margin-top: 15px;
  color: $color-primary;
  font-size: 16px;
}

.product-detail {
  background-color: #f6f6f6;
  padding-bottom: 16px;
}

.product-detail__inner {
  display: flex;
  flex-direction: column;

  @include mq(sm) {
    flex-wrap: wrap;
    flex-direction: row;
  }
}

.product-detail__main {
  @include mq(sm) {
    flex: 1 1 0;
  }
}

.product-detail__sidebar {
  @include mq(sm) {
    position: sticky;
    top: 50px;
    width: 275px;
    height: 100%;
    margin-left: 16px;
  }

  @include mq(ml) {
    top: 0;
  }
}

.section-box {
  background-color: $color-white;
  margin-top: 16px;
}

.section-box__title {
  padding: 20px;
  border-bottom: 1px solid #cccccc;
}

.section-box__title--red {
  color: $color-primary;
}

.section-box__text {
  font-size: 16px;
  font-family: $font-roboto-bold;
  text-transform: uppercase;
}

.section-box__content {
  padding: 25px;
}

.product-card {
  margin-bottom: 40px;
}

.product-card__inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include mq(sm) {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.product-card__view {
  width: 100%;
  border: 1px solid #eeeeee;

  @include mq(sm) {
    width: 40%;
  }
}

.product-card__img {
  @include aspect(174, 320);
}

.product-card__content {
  padding-top: 20px;

  @include mq(sm) {
    padding-top: 0;
    padding-left: 40px;
    width: 60%;
  }
}

.product-card__link {
  display: block;
  font-size: 16px;
  font-family: $font-roboto-bold;
  color: $text-color-primary;
}

.product-card__price {
  margin-top: 5px;
  font-size: 16px;
  font-family: $font-roboto-bold;
  color: $color-primary;
}

.product-card__description {
  margin-top: 10px;
}

.product-card__button {
  border-radius: 20px;
}

.product-booking__title {
  display: block;
  color: #606060;
  font-family: $font-roboto-bold;
  margin-bottom: 10px;
}

.product-booking__entry {
  margin-bottom: 22px;

  .form-control {
    margin-bottom: 15px;
    border: none;
    border-bottom: 1px dashed #ccc;
    border-radius: 0;
  }
}

.product-booking__form {
  margin-bottom: 20px;

  .form-control {
    height: calc(1.5em + 0.75rem - 2px);
    border-radius: 0;
  }
}

.product__info-link {
  display: block;
}

.product-booking__entry-wrapper {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    top: 15px;
    left: 0;
    width: 2px;
    height: calc(100% - 30px);
    border-radius: 3px;
    background: #afafaf;
    z-index: 9;
  }
}

.product-booking__entry-box {
  position: relative;
  padding-left: 20px;

  &:before {
    content: '';
    position: absolute;
    top: 15px;
    left: 1px;
    width: 8px;
    height: 8px;
    background: #fff;
    border: 2px solid #a7a7f5;
    border-radius: 50%;
    -webkit-transform: translate(-50%);
    transform: translate(-50%);
    z-index: 10;
  }

  &.product-booking__entry-box--add {
    &:before {
      background: #a7a7f5;
    }
  }
}
